body{
    color: black;
    background-color: white;
  }
  
  .header_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    font-size: 5vw;
    font-weight: bold;
    line-height: 15vw;
  }
  
  .header_item {
    cursor: pointer;
    margin-left: 3vw;
    margin-right: 3vw;
  }
  
  .header_item img {
    width: 4vw;
    height: 4vw; 
  }
  
  .header_item_blank {
    flex-grow: 1;
  }
  
  .title_container {
    padding-left: 5%;
    padding-right: 5%;
    font-size: 3vw;
    margin-bottom: 8%;
  }
  
  .finder_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    justify-content: space-around;
    padding-left: 5%;
    padding-right: 5%;
    font-size: 3vw;
  }
  
  .finder_button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    cursor: pointer;
    padding-top: 3vw;
    padding-bottom: 3vw;
    padding-left: 8vw;
    padding-right: 8vw;
    border: 2px solid rgb(200, 200, 200);
    border-radius: 30% / 100%;
  }
  
  .finder_button img {
    width: 3vw;
    height: 3vw;
    margin-right: 2vw;
  }
  
  .subtitle_container{
    margin-top: 8%;
    margin-bottom: 5%;
    padding-left: 5%;
    padding-right: 5%;
    font-weight: bold;
    font-size: 3vw;
  }
  
  .lazy_container img {
    width: 100%;
    height: auto;
    cursor: pointer;
  }