.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    background-color: #fff;
    overflow-x: hidden;
    white-space: nowrap;
    transition: 0.5s;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
  }

  .sidenav::-webkit-scrollbar {
    display: none;
  }
  
  .sidenav a:hover, .offcanvas a:focus{
    color: #f1f1f1;
  }
  
  .sidenav .closebtn {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
    color: white;
  }
  
  .sidenav_header {
    padding: 32px 8px 8px 32px;
    color: #f1f1f1;
    background-color: #919EA6;
    height: 130px;
    margin-bottom: 20px;
    text-align: left;
  }
  
  .sideitem_main a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 20px;
    font-weight: bold;
    color: black;
    display: flex;
    transition: 0.3s;
  }

  .sideitem_sub {
    font-size: 18px;
    color: black;
  }

  .sideitem_sub div {
    padding: 8px 8px 8px 32px;
    display: flex;
  }

  .sideitem_sub a {
    padding: 4px 8px 4px 32px;
    text-decoration: none;
    display: flex;
    justify-items: center;
    cursor: pointer;
    line-height: 40px;
    transition: 0.3s;
  }

  .sideitem_sub img {
    width: auto;
    height: 35px;
    margin-right: 8px;
  }
  
  .sidedim {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
  }

  .sidenav_divider {
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 15px;
    margin-bottom: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }