.App {
  text-align: center;
  color: white;
  background-color: #6e928e;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: center;
  font-size: 5vw;
  font-weight: bold;
  line-height: 15vw;
  margin-bottom: 2vw;
  padding-left: 2vw;
  padding-right: 2vw;
  cursor: pointer;
}

.header_item {
  cursor: pointer;
  margin-left: 1vw;
  margin-right: 1vw;
}

.header_item_blank {
  flex-grow: 1;
}

.header_item img {
  width: 4vw;
  height: 4vw;
}

.item_container {
  padding-left: 3%;
  padding-right: 3%;
  margin-bottom: 2%;
  display: flex;
}

.add_item {
  width: 30vw;
  height: 30vw;
  line-height: 30vw;
  border: 2px solid rgb(200, 200, 200);
  border-radius: 10%;
  text-align: center;
  cursor: pointer;
  font-size: 8vw;
}

.banner_container {
  padding-left: 3%;
  padding-right: 3%;
}

.banner_container img {
  width: 100%;
  height: auto;
  cursor: pointer;
  filter: drop-shadow(3px 3px 4px #111);
  margin-top: 2%;
}